import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, compareJSON, fetchPostH, getUrlServerSide, ObjectKeys, parseDateMMDDYY, parseDateMMDDYYYY } from "../../../stores/Utils";
import { generateTranslateBirthCertification } from "../services";
import {  TextInput_Cmpt } from "../../../components";
import { DateCalComponent_Cmpt } from "../../Inventory";
import {  NotaryAgentPick_Cmpt } from "../../Customer";
import { OpenModal } from "../../../components/DialogHRM";


const DateCalComponent =DateCalComponent_Cmpt();


const TextInput = TextInput_Cmpt();


const NotaryAgentPick = NotaryAgentPick_Cmpt();





const frmNm = "birth_certificate"


let flds = [
	"folio",
	"tomo",
	"state_registration",
	"registration_officer",
	"revised_by",
	"prepared_by",
	"city_office",
	"state_office",
	"city_registration",
	"dop",
	"dor",
	"grandFather_FatherSide",
	"grandMother_FatherSide",
	"grandFather_MotherSide",
	"grandMother_MotherSide",
]

const BirthCertification  = ({ data, elmId, close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);




	const loadDoc  = async ()  => {
		let bdyq2 = {
			query: "getScanDocumentsNotary",
			params: {
				":customerId" : getStore("upd_client").clientNotaryId,
				":documentType": "BCT"
			},
			queryString: "customerId contain :customerId AND documentType contain :documentType",
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && !td.error) {

				let docId = ObjectKeys(td.data)[0];

				if(td.data[docId]){
					updStore(frmNm, td.data[docId]);
					updStore(frmNm + "_bck", JSON.parse(JSON.stringify(td.data[docId])));
					set_obs(gen16CodeId())
				}
				else{
					updStore(frmNm, null);
					set_obs(gen16CodeId())
				}
			
		}

	}


	
	useEffect(() => {
		if (!initialize) {
			
			setInitialize(true);
			updStore(frmNm, null);
			set_obs(gen16CodeId())

			

			

			setTimeout(() => {
				loadDoc();
				let user = getStore("upd_client")
		

				let frm_userInfo = {
					
					"city_pob":	user?.placeOfBirth?.city,
					"state_pob":user?.placeOfBirth?.state,
					
					
					"genre":user?.genre,
					"dob":	user?.dob,
					"dateOfBirth": user?.dateOfBirth,

					
					"name":user?.firstName ? `${user?.firstName} ${user?.middleName?user?.middleName+" ":""}${user?.lastName} ` :"",
					
				}

				if(user?.fatherInfo?.firstName){
					frm_userInfo["fatherBirthPlace"]= user?.fatherInfo?.placeOfBirth?.city+", "+ user?.fatherInfo?.placeOfBirth?.state;
					frm_userInfo["fatherName"]=	user?.fatherInfo?.firstName ? `${user?.fatherInfo?.firstName} ${user?.fatherInfo?.middleName?user?.fatherInfo?.middleName+" ":""}${user?.fatherInfo?.lastName} ` :"";
				} 
				
				if(user?.motherInfo?.firstName){
					frm_userInfo["motherBirthPlace"]=	user?.motherInfo?.placeOfBirth?.city +", "+ user?.motherInfo?.placeOfBirth?.state;
					frm_userInfo["motherName"]=	user?.motherInfo?.firstName ? `${user?.motherInfo?.firstName} ${user?.motherInfo?.middleName?user?.motherInfo?.middleName+" ":""}${user?.motherInfo?.lastName} ` :"";
				
				} 
				
				updStore(frmNm+"_userInfo", frm_userInfo);



				set_obs(gen16CodeId())
			}, 50);
			return () => {
				updStore(frmNm, null);
			}; 
		}
	});

	const updFrm = (f, v) => {
		let frm2 = getStore(frmNm) || {};
		frm2[f] = v;
		updStore(frmNm, frm2);
		set_obs(gen16CodeId())
	};



	const save  = async ()  => {

		let ffm =  getStore(frmNm);

		let bdyq2 = {};
		if(ffm["documentNotaryId"]){
			let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )
		
			bdyq2 = {
				query: "updateDocumentsNotary",
				params:{
					"documentNotaryId": ffm["documentNotaryId"]
				},
				data2update: diff.data
			};
		}
		else{
			ffm["customerId"] = getStore("upd_client").clientNotaryId;
			ffm["documentType"] = "BCT";
			ffm["documentNotaryId"] = gen16CodeId();
			bdyq2 = {
				query: "addDocumentsNotary",
				form:ffm
			};
		}

		
	
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			updStore(frmNm, td.data);
			set_obs(gen16CodeId());
		}
	}


	const print  = async ()  => {
		let ffm = Object.assign({}, getStore(frmNm),getStore(frmNm+"_userInfo"));

		let gF_Fs = ffm["grandFather_FatherSide"] ? ffm["grandFather_FatherSide"]: "";
		let gM_Fs = ffm["grandMother_FatherSide"] ? ffm["grandMother_FatherSide"]: "";
		
		let gF_Ms = ffm["grandFather_MotherSide"] ? ffm["grandFather_MotherSide"]: "";
		let gM_Ms = ffm["grandMother_MotherSide"] ? ffm["grandMother_MotherSide"]: "";
		
		let has2gF = gF_Fs && gM_Fs ? " and ":"";
		let has2gM = gF_Ms && gM_Ms? " and ":"";
		
		ffm["grands_FatherSide"] = gF_Fs + has2gF + gM_Fs;
		ffm["grands_MotherSide"] = gF_Ms + has2gM + gM_Ms;

		
		
		if(ffm["dor"]){
			let nd = (new Date(ffm["dor"])).getTime();
			if(nd){
				ffm["dor_p"] = parseDate(nd);
			}
		}

		if(ffm["dop"]){
			let nd = (new Date(ffm["dop"])).getTime();
			if(nd){
				ffm["dop_p"] = parseDate(nd);
			}
		}

		if(ffm["dateOfBirth"]){
			ffm["dob_p"] = parseDate(ffm["dateOfBirth"]);
		}
		
		ffm["todayDate"] =  parseDateMMDDYYYY((new Date()).getTime());

		console.log(ffm)
		

		let pdfUri = await generateTranslateBirthCertification(ffm, true, true);
		window.open(pdfUri);
	};


	
	

	const handleNotary  =  (f,v)  => {
		updFrm(f,v)
		//set_obs(gen16CodeId())
	}

	const openAddNotary =  (f) => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <NotaryAgentPick updObs={set_obs} confirm={(v)=>handleNotary(f,v)} />
		OpenModal(dataModal);
	}


	return (
		<div style={{
			maxHeight: "80vh",
			width: "800px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Traducion de Certificacion de Nacimiento
				</p>
				<div className="flexSpace"/>
				<button  className="group_title addBtn purple" onClick={print}>
					Imprimir
				</button>
			</div>
			
			
			<p className="group_title">
				Lugar y datos del Registro 
			</p>
		

			<div className="_dsplFlx spaceAround"> 
				<TextInput  init={getStore(frmNm)?.["folio"]} label={"Folio"} width={60} updChanges={(e)=>updFrm("folio",e)}/>
				<TextInput  init={getStore(frmNm)?.["tomo"]} label={"Tomo"} width={60} updChanges={(e)=>updFrm("tomo",e)}/>
				<DateCalComponent  
					init={getStore(frmNm)?.["dor"]} 
					label={"Fecha de registro"}
					updDate={(e)=>updFrm("dor",e)}
				/>
			</div>
			<div className="_dsplFlx spaceAround"> 
				<TextInput  init={getStore(frmNm)?.["city_registration"]} label={"Municipio"} width={240} updChanges={(e)=>updFrm("city_registration",e)}/>
				<TextInput  
					init={getStore(frmNm)?.["state_registration"]} 
					label={"Provincia"} width={240} 
					updChanges={(e)=>updFrm("state_registration",e)}
				/>
			</div>
			<div className="separator"/>
			
			<p className="group_title">
				Oficina donde se solitito el Registro 
			</p>
			<div className="_dsplFlx spaceAround "> 
				<DateCalComponent  
					init={getStore(frmNm)?.["dop"]} 
					label={"Fecha de solicitud"} 
					updDate={(e)=>updFrm("dop",e)}
				/>
				<TextInput  init={getStore(frmNm)?.["city_office"]} label={"Municipio"} width={240} updChanges={(e)=>updFrm("city_office",e)}/>
				<TextInput  init={getStore(frmNm)?.["state_office"]} label={"Provincia"} width={240} updChanges={(e)=>updFrm("state_office",e)}/>
			</div>
			<div className="_dsplFlx spaceAround flxWrp" style={{margin: "22px 0 12px"}}> 
				
				<div>
					<p className="label" onClick={()=>openAddNotary("prepared_by")}>
						{"Preparado por:"}
					</p>
					<div className="date2Upd">
						{getStore(frmNm)?.["prepared_by"]}
					</div>
				</div>
				<div>
					<p className="label" onClick={()=>openAddNotary("revised_by")}>
						{"Revisado por:"}
					</p>
					<div className="date2Upd">
						{getStore(frmNm)?.["revised_by"]}
					</div>
				</div>

			
				<div>
					<p className="label" onClick={()=>openAddNotary("registration_officer")}>
						{"Firmado por:"}
					</p>
					<div className="date2Upd">
						{getStore(frmNm)?.["registration_officer"]}
					</div>
				</div>
				
			</div>
			

			
			<div className="separator"/>

			<p className="group_title">
				Nombre Abuelos Paternos
			</p>
				<div className="_dsplFlx spaceAround"> 
				<TextInput 
					init={getStore(frmNm)?.["grandFather_FatherSide"]} 
					label={"Abuelo"} 
					width={160} 
					updChanges={(e)=>updFrm("grandFather_FatherSide",e)}
				/>
				<div style={{margin: "0 4px"}}></div>
				<TextInput 
					init={getStore(frmNm)?.["grandMother_FatherSide"]} 
					label={"Abuela"} 
					width={160} 
					updChanges={(e)=>updFrm("grandMother_FatherSide",e)}
				/>
			</div>
			<p className="group_title">
				Nombre Abuelos Maternos
			</p>
				<div className="_dsplFlx spaceAround"> 
				<TextInput 
					init={getStore(frmNm)?.["grandFather_MotherSide"]} 
					label={"Abuelo"} 
					width={160} 
					updChanges={(e)=>updFrm("grandFather_MotherSide",e)}
				/>

				<div style={{margin: "0 4px"}}></div>
				<TextInput 
					init={getStore(frmNm)?.["grandMother_MotherSide"]} 
					label={"Abuela "} 
					width={160} 
					updChanges={(e)=>updFrm("grandMother_MotherSide",e)}
				/>
			</div>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<button className="group_title purple addBtn" onClick={save}>
					Guardar
				</button>
			</div>
		
		</div>
	);
};

export default BirthCertification;

